ops: {\n        id: null\n    },\n    data() {\n        return {\n            formData: \"\",\n            errors: {},\n            fetchLoading: false,\n            updateLoading: false\n        };\n    },\n    mounted() {\n        this.fetch();\n    },\n    methods: {\n        fetch() {\n            let self = this;\n            if (self.fetchLoading) {\n                return;\n            }\n            self.fetchLoading = true;\n            let data = {\n                id: self.$props.id\n            };\n            this.$store\n                .dispatch(\"profit/company/details\", data)\n                .then(response => {\n                    self.formData = response.data.data;\n                    self.fetchLoading = false;\n                })\n                .catch(error => {\n                    self.fetchLoading = false;\n                    self.$router.push({ name: \"Dashboard\" });\n                    self.$store.dispatch(\"snackbar/show\", {\n                        text: self.$t(\"error.nodata\")\n                    });\n                });\n        },\n        update() {\n            let self = this;\n            if (self.updateLoading) {\n                return;\n            }\n            self.updateLoading = true;\n            this.$store\n                .dispatch(\"profit/company/update\", self.formData)\n                .then(response => {\n                    self.formData = response.data.data;\n                    self.updateLoading = false;\n                    self.errors = {};\n                })\n                .catch(error => {\n                    self.updateLoading = false;\n                    self.errors = error.response.data?.data;\n                });\n        }\n    }\n};\n</script>\n\n<style scoped>\n.v-text-field >>> input {\n    font-size: 0.8em;\n    font-weight: 100;\n}\n.v-text-field >>> label {\n    font-size: 0.8em;\n}\n.v-text-field >>> button {\n    font-size: 0.8em;\n}\n</style>\n"],"sourceRoot":""}]);
